<template>
    <div class="maxBox">
        <DateSearch @func="changeDate" @selectVal='selectVal' :search="type"></DateSearch>
        <div class="number">
            <el-row>
            <el-col :span="5"><div class="grid-content ">
                <p>新增用户(昨日)</p>
                <span>{{userAllData.new_user.value}}</span>
                <span  class="goup" v-if="userAllData.new_user.rise>0">较前一日&nbsp;↑&nbsp;{{userAllData.new_user.grow}}&nbsp;</span>
                <span  class="decline" v-else-if="userAllData.new_user.rise<0">较前一日&nbsp;↓&nbsp;{{userAllData.new_user.grow}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{userAllData.new_user.grow}}&nbsp;</span>
                </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>活跃用户(昨日)</p>
                 <span>{{userAllData.active_user.value}}</span>
                <span  class="goup" v-if="userAllData.active_user.rise>0">较前一日&nbsp;↑&nbsp;{{userAllData.active_user.grow}}&nbsp;</span>
                <span  class="decline" v-else-if="userAllData.active_user.rise<0">较前一日&nbsp;↓&nbsp;{{userAllData.active_user.grow}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{userAllData.active_user.grow}}&nbsp;</span>
                </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>次日留存(昨日)</p>
                <span>{{userAllData.retained.value}}</span>
                <span  class="goup" v-if="userAllData.retained.rise>0">较前一日&nbsp;↑&nbsp;{{userAllData.retained.grow}}&nbsp;</span>
                <span  class="decline" v-else-if="userAllData.retained.rise<0">较前一日&nbsp;↓&nbsp;{{userAllData.retained.grow}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{userAllData.retained.grow}}&nbsp;</span>
                </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>付费用户(昨日)</p>
                <span>{{userAllData.paid_user.value}}</span>
                <span  class="goup" v-if="userAllData.paid_user.rise>0">较前一日&nbsp;↑&nbsp;{{userAllData.paid_user.grow}}&nbsp;</span>
                <span  class="decline" v-else-if="userAllData.paid_user.rise<0">较前一日&nbsp;↓&nbsp;{{userAllData.paid_user.grow}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{userAllData.paid_user.grow}}&nbsp;</span>
                </div>
            </el-col>
            <el-col :span="4"><div class="grid-content ">
                <p>累计用户</p>
                 <span>{{userAllData.cumulative.value}}</span>
                <span  class="goup" v-if="userAllData.cumulative.rise>0">较前一日&nbsp;↑&nbsp;{{userAllData.cumulative.grow}}&nbsp;</span>
                <span  class="decline" v-else-if="userAllData.cumulative.rise<0">较前一日&nbsp;↓&nbsp;{{userAllData.cumulative.grow}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{userAllData.cumulative.grow}}&nbsp;</span>
                </div>
            </el-col>
            </el-row>
        </div>
        <div class="phoneStatus">
           <div class="list"> 
                <span v-for="(item, index) in userType.list" :key="item.id" @click="userTypeChange(index,item)" :class="userType.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span> 
            </div>
            <div id="statusChart">
                 
            </div>
        </div>
        <div class="infoTable">
            <span class="outTable" @click="exportData">导出</span>
             <el-collapse v-model="activeNames" @change="handleChange" >
                <el-collapse-item title="收起明细数据" name="1">
                    <div class="contentTab" ref="contentTab">
                         <el-table :data="tableData" style="width: 100%;marginLeft:-20px;">
                            <el-table-column
                                prop="time"
                                label="日期"
                                width="180">
                            </el-table-column>
                            <el-table-column
                                prop="new_user"
                                label="新增用户"
                                width="180">
                            </el-table-column>
                            <el-table-column
                                prop="active_user"
                                label="活跃用户">
                            </el-table-column>
                              <el-table-column
                                prop="retained"
                                label="次日留存">
                            </el-table-column>
                              <el-table-column
                                prop="paid_user"
                                label="付费用户">
                            </el-table-column>
                              <el-table-column
                                prop="cumulative"
                                label="累计用户">
                            </el-table-column>
                        </el-table>
                       <Pagination
                        @handleCurrentChange="handleCurrentChange"
                        @handleSizeChange="handleSizeChange"
                        :total="totalNum"
                        :currentPage="currentPage"
                        :isTool='false'
                        />
                    </div>
                </el-collapse-item>
                </el-collapse>
        </div>
        <div class="box channel">
            <div class="titleBar">
              <span>渠道分析</span>
            </div>
            <div class="list"> 
                 <span v-for="(item, index) in channelUserType.list" :key="item.id" @click="channelUserTypeChange(index,item)" :class="channelUserType.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span> 
            </div>
           <el-row>
            <el-col :span="24"><div class="grid-content" id="channelAnalysis"></div></el-col>
          </el-row>
        </div>
    </div>
</template>
<script>
import DateSearch from './components/DateSearch'
import { EleResize} from "../../utils/resize";
import { getUserCount,exportCountData} from '../../api/statictics';
import Pagination from '../../components/Pagination'
export default {
    data() {
        return {
        time:'',
        type:true,
        channel:'',
        activeNames: ['1'],
        userType:{
             index:0,
             user:'new',
             list:[
                 {
                     id:'0',
                     name:'new',
                     value:'新增用户'
                 },
                 {
                     id:'1',
                     name:'active',
                     value:'活跃用户'
                 },
                 {
                     id:'2',
                     name:'retained',
                     value:'次日留存'
                 },
                 {
                     id:'3',
                     name:'paid',
                     value:'付费用户'
                 },
                 {
                     id:'4',
                     name:'cumulative',
                     value:'累计用户'
                 }]
           },
        tableData:[],
        currentPage: 1,
        totalNum: 0,
        pageSize: 6,
        userAllData:{
                new_user:{
                    grow:'',
                    rise:'',
                    value:''
                },
                active_user:{
                    grow:'',
                    rise:'',
                    value:''
                },
                retained:{
                    grow:'',
                    rise:'',
                    value:''
                },
                paid_user:{
                    grow:'',
                    rise:'',
                    value:''
                },
                cumulative:{
                    grow:'',
                    rise:'',
                    value:''
                },
           }, //用户总数返回结果
        channelUserType:{
            index:0,
            user:'new',
            list:[ {
                     id:'0',
                     name:'new',
                     value:'新增用户'
                 },
                 {
                     id:'2',
                     name:'paid',
                     value:'付费用户'
                 }]
        },
        isStart:false,//判断首次是否通过日期监听
        };
    },
    mounted(){
      this.userCount();
      this.userCountChart();
      this.tableListFun();
      this.channelAnalysis();
      let height = document.querySelector(".contentTab").offsetHeight;
      document.querySelector(".channel").style.marginTop = height;
    },
    
    methods:{
        /**
         * 平台类型判断
         */
      selectVal(val){
       this.channel= val;
       this.userCount();
       this.userCountChart();
       this.tableListFun();
       this.channelAnalysis();
      },
       /**
       * 日期返回值
       */
      changeDate(msg){
         this.time = msg;
        if(this.isStart){
            this.userCountChart();
            this.tableListFun();
            this.channelAnalysis();
        }else{
          this.isStart = !this.isStart;
        }
      },
      handleCurrentChange(val) {
            this.currentPage = val;
            this.tableListFun();
        },
      handleSizeChange(val) {
            this.pageSize = val;
            this.tableListFun();
        },
      /**
       * 用户统计，对比
       */
      userCount(){
         let params = new Object();
             params.format = 'yesterday';
             params.channel = this.channel;
            getUserCount(params)
            .then(res => {
                if (res.code == 200) {
                    this.userAllData = res.data
                }
            })
            .catch(error => {
                console.log(error)
            });
      },
      /**
       * 用户类型选择
       */
      userTypeChange(index,item){
         this.userType.index = index;
         this.userType.user = item.name;
         this.userCountChart();
      },
      channelUserTypeChange(index,item){
         this.channelUserType.index = index;
         this.channelUserType.user = item.name;
         this.channelAnalysis();
      },
        /**
         * 用户数量折线图
         */
        userCountChart (){
          let params = new Object();
             params.format = 'line';
             params.channel = this.channel;
             params.item = this.userType.user;
             params.firstDay = this.time[0];
             params.lastDay = this.time[1];
            getUserCount(params)
            .then(res => {
                if (res.code == 200) {
                     let myChart = this.$echarts.init(document.getElementById('statusChart'));
                      let option = {
                            grid:{
                              top:'5%',
                              left:'1%',
                              right:'2%',
                              bottom:'8%',
                              containLabel:true
                              },
                              tooltip: {
                              trigger: 'axis'
                              },
                              xAxis: {
                                  type: 'category',
                                  data: res.data.time,
                              },
                              yAxis: {
                                  type: 'value',
                                  axisLine:{    
                                    show:false
                                  },
                                  axisTick:{    
                                    show:false
                                  },
                                  splitLine: { 
                                      lineStyle:{
                                        type:'dashed'
                                  }
                                      }                                     
                              },
                              series: [{
                                  name: "用户人数",
                                  data: res.data.value,
                                  type: 'line',
                                  itemStyle : {  
                                          normal : {  
                                              color:'#409eff',  
                                              lineStyle:{  
                                                  color:'#409eff'  
                                              }  
                                          }  
                                      },  
                              }]
                          };
                      myChart.setOption(option);
                      let resizeDiv = document.getElementById('statusChart')
                      let listener  =function(){
                        myChart.resize();
                      }
                      EleResize.on(resizeDiv,listener);
                }
            })
            .catch(error => {
                console.log(error)
            });         
        },
        /**
         * 表格数据导出
         */
        exportData(){
          let params = new Object();
             params.channel = this.channel;
             params.firstDay = this.time[0];
             params.lastDay = this.time[1];
            exportCountData(params)
            .then(res => {
              
            })
            .catch(error => {
                console.log(error)
            });
        },
        /**
         * 表格数据
         */
        tableListFun(){
            let params = new Object();
             params.format = 'table';
             params.channel = this.channel;
             params.firstDay = this.time[0];
             params.lastDay = this.time[1];
             params.page = this.currentPage
             params.pageSize =  this.pageSize;
            getUserCount(params)
            .then(res => {
                if (res.code == 200) {
                   this.tableData =res.data.list;
                   this.totalNum = res.data.total_number;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        /**
         * 收起明细数据折叠
         */
        handleChange(val) {
          let height  ='';
            if(val.length>0){
              document.querySelector(".channel").style.marginTop = height;
            }else{
              document.querySelector(".channel").style.marginTop ='15px';
              height = document.querySelector(".contentTab").offsetHeight;
            }
        },
        /**
         *渠道分析
         */
        channelAnalysis(){
             let params = new Object();
             params.format = 'analysis';
             params.channel = this.channel;
             params.item = this.channelUserType.user;
             params.firstDay = this.time[0];
             params.lastDay = this.time[1];
            getUserCount(params)
            .then(res => {
                if (res.code == 200) {
                   let list = res.data;
                   let myChart = this.$echarts.init(document.getElementById('channelAnalysis'));
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                grid: {
                    top:'3%',
                    left: '3%',
                    right: '4%',
                    bottom: '15%',
                    containLabel: true
                },
                xAxis: {
                    type: 'value',
                    boundaryGap: [, 0.01]
                },
                yAxis: {
                    type: 'category',
                    data: ['Web', 'PC', 'Android', 'IOS',]
                },
                series: [
                    {
                        name: '数据',
                        type: 'bar',
                        barWidth: 15, // 柱子宽度
                        data: [list.web, list.pc,list.android,list.ios],
                        itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                   color:'#409eff',  
                                }  
                            }  
                        }, 
                    },
                
                ]
            };
             myChart.setOption(option);
              let resizeDiv = document.getElementById('channelAnalysis')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener); 
                }
            })
            .catch(error => {
                console.log(error)
            });
        }
    },
    components:{
        DateSearch,
        Pagination
      }
}
</script>
<style lang="less" scoped>
@import "index.less";
.null-box{
 margin-top: 15px;
}
.number{
  width: 100%;
  height: 180px;
  border: 1px solid #ccc;
  margin-bottom: 25px;
  .grid-content{
    p{
        text-align: center;
        font-size: 20px;
    }
    span{
        display: block;
        text-align: center;
        font-size: 40px;
    }
    .decline{
  font-size: 14px;
  color: rgba(67, 207, 124, 1);
}
.goup{
   font-size: 14px;
   color:red;
}
     .just{
         font-size: 14px;
         color:#ccc;
    }
}
}
.phoneStatus{
  width: 100%;
  height: 450px; 
  border: 1px solid #ccc;
  margin-top: -35px;
    .list{
     padding: 30px 0px 0px 15px;
     display: flex;
     span{
         padding-right: 10px;
         cursor: pointer;
         border: 1px solid #ccc;
        // margin: -5px;
        padding: 10px 15px;
        font-size: 14px;
        border-right: 0px;
     }
     span:last-child{
           border-right: 1px solid #ccc;
     }
     .select{
        color: #409EFF;

     }
 }
 #statusChart {
    height: 80%;
 }
}
.infoTable{
  width: 100%;
  height: 70px; 
  border: 1px solid #ccc;
  border-top: none;
  border-bottom: none;
  .outTable{
    position: relative;
    left: 97%;
    top: 50%;
    color: #409EFF;
    cursor: pointer;
  }
   .el-collapse-item{
         margin-left: 20px;
        /deep/ .el-collapse-item__header{
            color: #409EFF;
            width:130px;
            font-size: 16px;
         }
        
     }
     /deep/ .el-pagination{
       text-align: right;
       margin-top: 15px;
     }  
}
.channel{
  width: 100%;
  height: 400px; 
  border: 1px solid #ccc;
  margin-top: 425px;
  margin-bottom: 15px;
  .list{
        padding: 15px 0px 0px 15px;
        display: flex;
        span{
            padding-right: 10px;
            cursor: pointer;
            border: 1px solid #ccc;
            // margin: -5px;
            padding: 5px 15px;
            font-size: 14px;
            border-right: 0px;
        }
        span:last-child{
            border-right: 1px solid #ccc;
        }
        .select{
            color: #409EFF;
        }
    }
}
</style>


